let factorial=function(a){
var rez=1;	

if (a==0){return 1}

for (var i=2;i<a+1;i++){
rez=rez*i;
}
return rez
}

let multidigits=function(a){
		var mult=1;

	String(a).split('').forEach((digit)=>{
		mult=mult*parseInt(digit)
	})
	return mult;
}

