let eachdigitless=function(a,less){

var abs=Math.abs(a);

if (String(a)[0]<less){
	var flip=true;
}else{
	flip=false;
}

if (a==abs){
var neg=false
}else{
neg=true
}

a=abs;

var ind=String(a).indexOf('.');

var b= String(a).replace('.','');
var c= b.split('')
var r:any='';

c.forEach((x:any)=>{
  if (neg){
    r+=Math.abs(-x-less);
  }else{
   r+=Math.abs(x-less);
}
    })
r=parseInt(r);

if (neg){

if (ind>-1){
  r=String(r).slice(0,ind)+'.'+String(r).slice(ind)
}
r=parseFloat(r);
r=r*-1;

}else{

if (ind>-1){
 
  r=String(r).slice(0,ind)+'.'+String(r).slice(ind)
  r=parseFloat(r);
}
}
if (flip){
	r=r*-1;
}
return r
}

let concatanationtimes = function(a,b,times){

var neg=false;
/**/
if (String(a).includes('.') && String(b).includes('.')){
  return 'nono';
}

var absa = Math.abs(a);
var absb= Math.abs(b);

if (a!==absa){neg=!neg}
if (b!==absb){neg=!neg}
//console.log(String(absb))

//var removezero=false


var r:any=String(absa)+String(absb)

if (String(absb)[0]=='0' && String(absb)[1]=='.'){
r=r.replace('0.','.')
}


r= parseFloat(r)

if (neg){
  r=-r
}
r=r*times;

return r

}


